import { Component, OnInit, Input, AfterViewInit, AfterContentChecked, Output, EventEmitter } from '@angular/core';
import { StorageService } from 'src/app/services/storage.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-info',
  templateUrl: './info.component.html',
  styleUrls: ['./info.component.scss']
})
export class InfoComponent implements OnInit, AfterViewInit, AfterContentChecked {
  @Input() location;
  faveLocation:boolean = false;
  favesList: number[];

  constructor(
    private storage: StorageService
  ) { }
// TODO: set up the observables here
  ngOnInit() {
    this.storage.faves.subscribe(faves => {
      this.favesList = faves;
      this.checkIfFave();
    });
  }

  ngAfterContentChecked() {
    this.storage.faves.subscribe(faves => {
      this.favesList = faves;
      this.checkIfFave();
    });
  }

  ngAfterViewInit() {
    this.checkIfFave();
  }


  onFaveBtnClick() {
    this.location.fave = !this.location.fave;
    this.storage.storeFavourite(this.location);
  }
  
  onFaveRemoveClick() {
    this.location.fave = !this.location.fave;
    this.storage.removeFavourite(this.location);
  }

  checkIfFave() {
    if (this.favesList && this.location){
      this.faveLocation = this.favesList.indexOf(this.location.number) >= 0;
    }
  }


}
