import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { StorageService } from 'src/app/services/storage.service';

@Component({
  selector: 'app-favourites',
  templateUrl: './favourites.component.html',
  styleUrls: ['./favourites.component.scss']
})
export class FavouritesComponent implements OnInit {
  locationsList;
  favesList = [];
  faves;

  @Output() notifyMap: EventEmitter<any> = new EventEmitter();

  constructor(
    private storage: StorageService
  ) { }

  ngOnInit() {
    this.storage.faves.subscribe(faves => {
      this.favesList = faves;
      if (this.locationsList) this.faves = this.locationsList.filter(l => this.favesList.indexOf(l.number) >= 0);
    });
    this.storage.locations.subscribe(locations => {
      this.locationsList = locations;
      this.faves = locations.filter(l => this.favesList.indexOf(l.number) >= 0)
    });
  }

  sendPanToLocation(location) {
    // console.log(location);
    this.notifyMap.emit(location);
  }

  removeFave(l) {
    if (confirm("Remove from favourites?")) this.storage.removeFavourite(l);
  }

}
