import { Injectable } from '@angular/core';
import { Location } from  '../interfaces/location';
import { Subject, BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class StorageService {

  private favesSource = new BehaviorSubject<any>(JSON.parse(localStorage.getItem('faves')) || []);
  public faves = this.favesSource.asObservable();
  private locationsSource = new BehaviorSubject<any>(JSON.parse(localStorage.getItem('locations')) || []);
  public locations = this.locationsSource.asObservable();

  constructor() {}

  public storeLocations(locations: any[]) {
    const savedFaves = localStorage.getItem('faves');
    const addFave = locations.map(l => {
      l.fave = savedFaves.indexOf(l.number) >= 0 ? true : false;
      return l;
    });
    localStorage.setItem('locations', JSON.stringify(addFave));
    this.locationsSource.next(addFave);
  }

  public getStoredLocations() {
    this.locationsSource.next(JSON.parse(localStorage.getItem('locations')));
  }

  public storeFavourite(location:Location) {
    if (!localStorage.getItem('faves')) localStorage.setItem('faves', '[]');
    let faves = JSON.parse(localStorage.getItem('faves'));
    const locations = JSON.parse(localStorage.getItem('locations'));
    if (faves.indexOf(location.number) < 0) {
      faves.push(location.number);
      localStorage.setItem('faves', JSON.stringify(faves));
      this.storeLocations(locations);
      this.favesSource.next(faves);
    }
  }

  public getFavourites() {
    this.favesSource.next(JSON.parse(localStorage.getItem('faves')));
  }

  public removeFavourite(location) {
    const locations = JSON.parse(localStorage.getItem('locations'));
    let faves = JSON.parse(localStorage.getItem('faves'));
    if (faves.indexOf(location.number >= 0)) {
      // console.log(faves, location.number);
      
      const newFaves = faves.filter(n => n != location.number)
      localStorage.setItem('faves', JSON.stringify(newFaves));
      this.storeLocations(locations);
      this.favesSource.next(newFaves);
    }
  }
}
