import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {

  ngOnInit() {
    if (!localStorage.getItem('faves')) localStorage.setItem('faves', '[]');
    if (!localStorage.getItem('locations')) localStorage.setItem('locations', '[]'); 
  }
}
